body {
  font-family: 'Lato', sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #e6f2ff;
}

input[type='text'] {
  display: inline-block;
  margin-right: 10px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
  margin-bottom: 10px;
  transition: all 0.3s ease-in-out;
}

input[type='text']:focus {
  outline: none;
  box-shadow: 0px 0px 5px rgba(0,119,204,0.5);
}

.skills-filter {
 display:flex; 
 flex-wrap:wrap; 
 margin-bottom:10px
}

.skills-filter label {
 display:flex; 
 align-items:center; 
 margin-right:10px; 
 margin-bottom:10px
}

.skills-filter input[type='checkbox'] {
 margin-right:5px
}

.employee {
 border-radius:5px; 
 box-shadow:0px 
0px 
5px rgba(0,0,0,.1); 
padding:20px; 
margin-top:10px; 
background-color:white; 
transition:all .3s ease-in-out; 
border-left:5px solid #0077cc
}

.employee:hover {
box-shadow:0px 
0px 
10px rgba(0,0,0,.3); 
border-left-color:#005999; 
transform:scale(1.02)
}

.employee p {
margin:0
}

.employee p + p {
margin-top:10px
}

.employee span {
display:inline-block; 
width:120px; 
text-align:right; 
background-color:#f2f2f2; 
padding-right:10px; 
font-weight:bold; 
margin-right:10px; 
border-radius:5px
}

.projects,
.team {
margin-left :20px; 
margin-top :10px; 
margin-bottom :10px; 
padding-left :15px; 
border-left :1px solid #ccc
}
.team-mem p + p{
margin-top :5px
}
.team-mem {
margin-top :10px
}

.undefined-value {
 display:inline-block; 
 width :120px; 
 padding-left :10px; 
 font-weight:bold; 
 margin-right :10px; 
 border-radius :5px; 
 background-color:#ff6666; 
 color:white
}
.skills-dropdown {
  position: relative;
}

.skills-dropdown button {
  display: inline-block;
  margin-right: 10px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
  margin-bottom: 10px;
  transition: all 0.3s ease-in-out;
  background-color: #E6FFFD;
}
.skills-dropdown button:hover {
  background-color: #AEE2FF;
}
.skills-dropdown button:focus {
  outline: none;
}

.skills-options {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  z-index: 1;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
  min-width: 200px;
}

.skills-options label {
 display:block; 
 padding :10px; 
 cursor:pointer
}

.skills-options label:hover {
 background-color:#f2f2f2
}

.skills-options input[type='checkbox'] {
 margin-right :5px
}
